import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from "react-redux"
import { Snackbar, Button } from '@material-ui/core';
import { Add } from '@material-ui/icons';
import { actions } from '../redux/_actions';
import Card from '../components/card';
import NoCard from '../components/nocard';
import Layout from './layout';
import api from '../api';

const Purchase = (props) => {
  const [error, setError] = useState(false);
  const [limit] = useState(12);
  const [skip, setSkip] = useState(0);
  const [size, setSize] = useState(0);
  const [cards, setCards] = useState([]);

  const { _id } = useSelector(state => state.auth);
  const dispatch = useDispatch();

  useEffect(() => {
    if (!_id) return
    loadCards();
  }, [_id]);

  const loadCards = () => {
    dispatch(actions.setLoading(true));
    api.getProducts({ skip, limit, purchased: 1, _id }).then((data) => {
      dispatch(actions.setLoading(false));
      if (data.error) {
        setError(data.error);
      } else {
        setCards(data.data);
        setSize(data.size);
        setSkip(0);
      }
    });
  };

  const loadMore = () => {
    let toSkip = skip + limit;
    dispatch(actions.setLoading(true));
    api.getProducts({ skip: toSkip, limit, purchased: 1, _id }).then((data) => {
      dispatch(actions.setLoading(false));
      if (data.error) {
        setError(data.error);
      } else {
        setCards([...cards, ...data.data]);
        setSize(data.size);
        setSkip(toSkip);
      }
    });
  };

  const loadMoreButton = () => {
    return (
      size > 0 &&
      size >= limit && (
        <Button onClick={loadMore} variant='contained' className='more-btn'>
          more <Add />
        </Button>
      )
    );
  };

  return (
    <Layout>
      <div>
        <div className='d-flex flex-wrap justify-content-center'>
          {cards.map((card, i) => (
            <div key={i} className='shop-item'>
              <Card card={card}/>
            </div>
          ))}
        </div>
        {loadMoreButton()}
      </div>
      {!cards.length &&<NoCard />}
      <Snackbar open={!!error} autoHideDuration={6000} anchorOrigin={{vertical: 'top', horizontal: 'center'}} onClose={() => setError('')}>
        <div className="error-msg">{error}</div>
      </Snackbar>
    </Layout>
  );
};

export default Purchase;
