import React, { useState, useEffect } from 'react';
import { Snackbar, Button } from '@material-ui/core';
import { Add } from '@material-ui/icons';
import Card from '../components/card';
import NoCard from '../components/nocard';
import Layout from './layout';
import api from '../api';
import QuickView from '../components/quickview';
import { useDispatch } from 'react-redux';
import { actions } from '../redux/_actions';

const Main = () => {
  const [error, setError] = useState(false);
  const [limit] = useState(12);
  const [skip, setSkip] = useState(0);
  const [size, setSize] = useState(0);
  const [cards, setCards] = useState([]);
  const [quickViewCard, setQuickViewCard] = useState(null);

  const dispatch = useDispatch();

  useEffect(() => {
    loadCards();
  }, [])

  const loadCards = () => {
    dispatch(actions.setLoading(true));
    api.getProducts({ skip, limit }).then((data) => {
      dispatch(actions.setLoading(false));
      if (data.error) {
        setError(data.error);
      } else {
        console.log(data)
        setCards(data.data);
        setSize(data.size);
        setSkip(0);
      }
    });
  };

  const loadMore = () => {
    let toSkip = skip + limit;
    dispatch(actions.setLoading(true));
    api.getProducts({ skip: toSkip, limit }).then((data) => {
      dispatch(actions.setLoading(false));
      if (data.error) {
        setError(data.error);
      } else {
        console.log(cards, data)
        setCards([...cards, ...data.data]);
        setSize(data.size);
        setSkip(toSkip);
      }
    });
  };

  const loadMoreButton = () => {
    return (
      size > 0 &&
      size >= limit && (
        <Button onClick={loadMore} variant='contained' className='more-btn'>
          more <Add />
        </Button>
      )
    );
  };

  const putCart = (_id, shared, purchased) => {
    dispatch(actions.setLoading(true));
    api.putCart(_id, shared, purchased).then((data) => {
      dispatch(actions.setLoading(false));
      if (!data || data.error) {
        setError(data.error);
      } else {
        const idx = cards.findIndex(itm => itm._id === _id)
        if (idx < 0) return
        cards[idx].purchased = data.purchased;
        cards[idx].shared = data.shared;
        setCards([...cards]);
      }
    });
  }
  
  const removeClicked = (_id) => {
    dispatch(actions.setLoading(true));
    api.removeProduct(_id).then((data) => {
      dispatch(actions.setLoading(false));
      if (!data || data.error) {
        setError(data.error);
      } else {
        const idx = cards.findIndex(itm => itm._id === _id)
        if (idx < 0) return
        cards.splice(idx, 1);
        setCards([...cards]);
      }
    });
  }

  const itemClicked = (card) => {
    if (card.purchased) {
      putCart(card._id, 0, 0);
    } else if (card.shared) {
      putCart(card._id, 1, 1);
    } else {
      putCart(card._id, 1, 0);
    }
  }
  const shareClicked = (card) => {
    putCart(card._id, (card.shared ? 0 : 1), card.purchased);
  }
  const putPurchaseClicked = (card) => {
    putCart(card._id, card.shared, (card.purchased ? 0 : 1));
  }

  const quickView = (card) => {
    setQuickViewCard(card);
  }
  const previousQuickView = () => {
    const idx = cards.findIndex(itm => itm._id === quickViewCard._id);
    if (idx < 1) return;
    setQuickViewCard(cards[idx - 1]);
  }
  const nextQuickView = () => {
    const idx = cards.findIndex(itm => itm._id === quickViewCard._id);
    if (idx < 0) return;
    if (idx > cards.length - 2) {
      loadMore();
      return ;
    }
    setQuickViewCard(cards[idx + 1]);
  }

  return (
    <Layout>
      <div>
        <div className='d-flex flex-wrap justify-content-center'>
          {cards.map((card, i) => (
            <div key={i} className={'shop-item ' + (card.shared ? 'shared ' : '' ) + (card.purchased ? 'purchased ' : '' )} onClick={() => itemClicked(card)}>
              <Card card={card} remove={() => removeClicked(card._id)} quickView={() => quickView(card)}/>
            </div>
          ))}
        </div>
        {loadMoreButton()}
      </div>
      {!cards.length && <NoCard/>}
      {quickViewCard && <QuickView card={quickViewCard}
        previous={previousQuickView}
        next={nextQuickView}
        close={()=>setQuickViewCard(null)}
        share={() => shareClicked(quickViewCard)}
        putPurchase={() => putPurchaseClicked(quickViewCard)} />}
      <Snackbar open={!!error} autoHideDuration={6000} anchorOrigin={{vertical: 'top', horizontal: 'center'}} onClose={() => setError('')}>
        <div className="error-msg">{error}</div>
      </Snackbar>
    </Layout>
  );
};

export default Main;
